.historyBlock {
    flex-wrap: wrap;
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: center;
}

.imageBlock {
    display: flex;
    align-items: center;
    margin: 10px 0 10px 0;
    transform: rotate(6.14deg);
    width: 540px;
    height: 320px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

}

.descriptionBlock {
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.title {
    font-size: 24px;
    line-height: 95.5%;
    font-weight: 600;
    font-family: 'Gill Sans Bold', serif;
    margin-bottom: 20px;
}

.descriptionBlock h2 {
    font-size: 50px;
    margin-bottom: 20px;
}

.descriptionBlock b {
    font-family: 'Gill Sans Bold', serif;
}

.descriptionItem {
    margin-top: 20px;
    font-family: 'Gill Sans Bold', serif;
}

.bibleIcon{
    width: 26px;
    height: 21px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 5px 0 5px;
}

.biblePlaces {
    margin-top: 10px;
    font-family: "Gill Sans Light", serif;
    display: flex;
    color: #C4C4C4;
    font-style: italic;
    font-size: 14px;
    font-weight: 600;
    line-height: 18px;
    letter-spacing: 1px;
}

@media screen and (max-width: 1600px) {
    .imageBlock{
        width: 264px;
        height: 159px;
        transform: rotate(-13.06deg);
    }
    .descriptionBlock h2{
        font-size: 30px;
    }
    .title{
        font-size: 18px;
    }
    .historyBlock {
        justify-content: normal;
        align-items: center;
        flex-direction: column-reverse;
        text-align: center;
    }
    .descriptionBlock {
        width: 80%;
        height: 50%;
    }
}
